.listArea{
    width:100%;
    background-color: rgb(237, 245, 207);
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    align-items: center;
}
label{

    width: 100%;
    height: 40px;
    margin-top: 20px;
    display: inline-block;
    font-size: 18px;
}

input{

    padding: 10px;
    width: 500px;
    margin:10px 0px 20px 0px;
    border-radius: 3px;
    border:none;
    outline: none;
    font-size: 18px;
}

ul li{
    text-decoration: none;
    list-style: none;

}

.List{
    background-color: rgb(34, 32, 32);
    margin: 0;
    padding: 0;
    display: flex;
    flex-direction: column;
    border-radius: 3px;
}

.List li{
    padding: 10px;
    background-color: rgb(226, 248, 208);
    border-bottom: 1px solid rgb(191, 253, 179);
    display: flex;
    justify-content: space-between;
    align-items: center;
    width:500px;
}
.List li:nth-child(even){
    padding: 10px;
    background-color: rgb(178, 248, 121);
    border-bottom: 1px solid rgb(191, 253, 179);

    width:500px;
   
}
.name{
 
    width:200px;
    height: 100%;
    display: flex;
    justify-content: flex-start;
}
.tel{
    position: relative;
    float: right;
    width: 200px;
    height: 100%;


}

.forms{
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgb(34, 33, 33);
    box-sizing: border-box;
}
.forms input{
    margin:10px 0px 0px 0px;
    width: 100%;
    box-sizing: border-box;
}
.form-alt{
    width:520px;
    box-sizing: border-box;

}
.form-alt button{
    width:520px;
    box-sizing: border-box;
    padding: 10px;
    background-color: rgb(200, 247, 179);
    border:none;
    outline: none;
    border-radius: 3px;
    margin:10px 0px 10px 0px;
    font-size: 18px;
    color:rgb(22, 22, 22)
}
.sil:after{
    position: relative;
    content: "";
    width: 10px;
    height: 10px;
    border-radius: 50%;
    background-color: rgb(136, 135, 135);
    cursor: pointer;
    transition: 500ms;
    transform: scale(1);
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
.sil:hover:after{
    position: relative;
    content: "";
    width: 10px;
    height: 10px;
    border-radius: 50%;
    background-color: rgb(255, 83, 71);
    cursor: pointer;
    transition: 500ms;
    transform: scale(1.5);
    display: flex;
    justify-content: center;
    align-items: center;
  }
  